// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";
body {
	background-color: #f0f0f0;
	font-family: "Roboto", sans-serif;
	font-weight: 400;
}

.form {
	max-width: 690px;

	margin: 0 auto;
	background-image: url(../images/bg.png);
	padding-top: 30px;

	background-color: #fffefc;
	&__content {
		margin-left: 38px;
	}

	&__info {
		display: flex;
		background: url(../images/info-bg.png) no-repeat 99% 81%;

		flex-direction: row;
		position: relative;
		margin-bottom: 38px;
	}
	&__pic {
		align-self: flex-start;

		margin-right: 25px;
	}
	&__data {
		display: flex;
		flex-direction: column;
		input {
			background-color: transparent;
			text-transform: uppercase;
			padding-bottom: 6px;
			outline: none;
			font-size: 12px;
			margin-bottom: 33px;
			display: block;
			width: 470px;
			letter-spacing: 1.4px;
			border: none;
			border-bottom: 2px solid #000;
			color: rgb(1, 1, 1);
			text-align: left;
			&::placeholder {
			}
		}
	}
	&__skills {
		position: relative;
		&::before {
			display: block;
			content: "";
			height: 1px;
			width: 106%;
			position: absolute;
			top: -36px;
			background-color: #f0f0f0;
			left: -39px;
		}
		&::after {
			display: block;
			content: "";
			height: 2px;
			width: 106%;
			position: absolute;
			bottom: 0;
			background-color: #f0f0f0;
			left: -39px;
		}
		&-content {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			span {
				transition-duration: 400ms;
			}
			:checked + span {
				font-size: 12px;
				color: #000;
				text-decoration: underline;
			}
			label {
				width: 156px;
				margin-bottom: 44px;
				display: block;
				margin-right: 7px;
				letter-spacing: 1.3px;
				font-size: 12px;
				color: rgb(102, 102, 102);
				transition-duration: 250ms;
				&:hover {
					cursor: pointer;
					color: #000;
				}
				input {
					visibility: hidden;
					margin-right: 15px;
					&:hover {
						cursor: pointer;
					}
					position: relative;
					&::before {
						visibility: visible;
						width: 19px;
						display: block;
						position: absolute;
						height: 19px;
						top: -5%;
						left: 0px;
						z-index: 10000000;
						border-radius: 5px;
						background-color: rgb(238, 238, 238);
						content: "";
						transition-duration: 100ms;
					}
					&:checked {
						&::before {
							background: url(../images/sprites/png/check.png)
								no-repeat 50% 50%;
							background-color: rgb(0, 147, 215);
						}
					}
				}
			}
		}
	}
	&__heading {
		font-size: 18px;
		margin-bottom: 25px;
		letter-spacing: 0.4px;
	}
	&__level {
		margin-top: 59px;
		padding-bottom: 60px;
		display: flex;
		flex-direction: row;

		&-info {
			order: 2;
		}
		&-number {
			font-size: 50px;
			color: #0093d7;
			line-height: 0.44px;
			display: block;
			text-align: left;
			margin-top: 40px;
			margin-left: 8px;
		}
		&-text {
			max-width: 216px;
			font-size: 18px;
			font-family: "Roboto";
			color: rgb(1, 1, 1);
			text-align: left;
			letter-spacing: 0.24px;
			line-height: 1.37;
			margin: 5px 0 0px 9px;
		}
	}
	&__diagram {
		order: 1;
		position: relative;
		transition-duration: 500ms;
		margin-right: 65px;
		&-arrow {
			position: absolute;
			bottom: 2px;
			left: 33%;
			transform: rotate(-33deg);
		}
		&-arrow.yellow {
			transform: rotate(0deg);
			left: 33%;
			transition-duration: 700ms;
		}
		&-arrow.green {
			transform: rotate(57deg);
			left: 39%;
			transition-duration: 700ms;
		}
		&-arrow.blue {
			transform: rotate(126deg);
			left: 46%;
			transition-duration: 700ms;
		}
	}
}
@media screen and (max-width: 700px) {
	.form {
		&__skills {
			&-content {
				justify-content: space-around;
				padding: 0 8px;
				label {
					width: 130px;
					margin-bottom: 30px;
				}
			}
			&::before {
				width: 100%;
				left: 0;
			}
			&::after {
				width: 100%;
				left: 0;
			}
		}
		&__heading {
			margin-left: 15vw;
		}
		&__content {
			padding: 0 15px;
			margin-left: 0;
		}
		&__info {
			flex-direction: column;
		}
		&__pic {
			align-self: center;
			margin-bottom: 20px;
		}
		&__data {
			align-self: center;
			width: 100%;
			padding-left: 15%;
			input {
				max-width: 85%;
			}
		}
		&__diagram {
			order: 1;
			margin-right: 0;
			margin-bottom: 20px;
		}
		&__level {
			margin-top: 40px;
			align-content: center;
			align-items: center;
			flex-direction: column;
			padding-bottom: 20px;
			&-info {
				order: 2;
			}
		}
	}
}
